import request from './request'

/**/
export function  sendEmailCode(email){
    return request({
        url: 'https://dev.litepress.cn/user/wp-json/common/send-email-code ',
        method: 'post',
        data:email
    })
}

/**/
export  function getAvatars()  {
    return request({
        url: '/cravatar/avatars',
        method: 'get',
    })
}
/**/
export  function postAvatars(email,email_code,image_id)  {
    return request({
        url: '/cravatar/avatars',
        method: 'post',
        data: {email, email_code, image_id}
    })
}

/**/
export function  putAvatars(image_id,avatar_id){
    return request({
        url: '/cravatar/avatars/'+avatar_id,
        method: 'put',
        data: {image_id: image_id}
    })
}


/**/
export function  deleteAvatars(id){
    return request({
        url: '/cravatar/avatars/'+id,
        method: 'delete',
    })
}

// 
/**/
export  function getImages()  {
    return request({
        url: '/cravatar/images',
        method: 'get',
    })
}

/**/
export  function postImages(formData)  {
    return request({
        url: '/cravatar/images',
        method: 'post',
        data:formData
    })
}

/**/
export function  deleteImages(id){
    return request({
        url: '/cravatar/Images/'+id,
        method: 'delete',
    })
}
